  width: 80%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  height: calc(100vh - 60px);
}
.message {
  margin: 10px 0;
}
.user-message {
  background-color: #e0f7fa;
  padding: 10px;
  border-radius: 5px;
  align-self: flex-end;
  max-width: 60%;
  white-space: pre-wrap;
}
.bot-message {
  background-color: #ffecb3;
  padding: 10px;
  border-radius: 5px;
  align-self: flex-start;
  max-width: 60%;
  white-space: pre-wrap;
}

.sys-message {
  background-color: #ffcdd2;
  padding: 10px;
  border-radius: 5px;
  align-self: center;
  max-width: 60%;
  white-space: pre-wrap;
}

</style>
